<template>
  <div class="singlePage secondary-page-mode">
     <div class="secondary-toolbar">
          <span>时间选择 : </span>&nbsp;
          <div class="layoutBox" style="width:380px">
            <!-- <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"
            value-format="yyyy-MM-dd"></el-date-picker> -->
            <el-date-picker
              v-model="dateRange"
              type="datetimerange"
              align="left"
              format = 'yyyy-MM-dd HH:mm:ss'
              value-format = 'yyyy-MM-dd HH:mm:ss'
              start-placeholder="开始日期"
              end-placeholder="结束日期">
            </el-date-picker>
          </div>&emsp;&emsp;
          <span>监测项目 : </span>&nbsp;
          <div class="layoutBox" style="width:120px">
            <el-select v-model="factorModel"  placeholder="请选择" @change="selectChange">
                  <el-option
                    v-for="item in factorData"
                    :key="item.projectCode"
                    :label="item.projectName"
                    :value="item.projectCode">
                  </el-option>
            </el-select>
          </div>&emsp;&emsp;
          <el-button @click="getEchartData"><i class="iconfont icon-sousuo"></i>查询</el-button>
        
     </div>
     <div style="position:relative" class="currency-mode-wrap"  v-loading="loading"  element-loading-text="加载中"
               element-loading-spinner="loading-type-a"
               element-loading-background="rgba(255, 255, 255, 1)">
           <div class="echartbox" id="echartbox">

           </div>
           <yf-Datastatus :dataStatus="dataStatus" v-if="dataStatus == 'no'"></yf-Datastatus>
     </div>
      
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      dateRange: [],
      factorModel:'',
      factorData: [],
      stationId:'',
      loading:true,
      dataStatus:'',
    };
  },
  created() {
    this.initTime()
  },
  mounted() {
 
  },
  methods: {
    initTime(){
        //初始化时间
      let startTime = new Date().setDate(new Date().getDate() - 3);
      let endTime = new Date();
      this.dateRange = [
        new Date(startTime).format("yyyy-MM-dd hh:mm:ss"),
        endTime.format("yyyy-MM-dd hh:mm:ss")
      ];
    },
    initEchart(unit,factor,legendData,xAxisData,seriesData) {
      var option = {
        title: {
          text: factor+"变化趋势分析图",
          x: "center"
        },
        grid:{
          left:'10%',
          right:'10%',
          top:'10%',
          bottom:'10%'
        },
        tooltip: {
          trigger: "axis"
        },
        legend: {
          data: legendData,
          y: "bottom"
        },
        toolbox: {
          show: true,
          feature: {
            dataZoom: {
              yAxisIndex: "none"
            },
            magicType: { type: ["line", "bar"] },
            restore: {},
            saveAsImage: {}
          }
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data:xAxisData
        },
        yAxis: {
          type: "value",
          name: "监测值("+unit+")"
        },
        series: [
          {
            name: factor,
            type: "line",
            data: seriesData,
            markPoint: {
              data: [
                { type: "max", name: "最大值" },
                { type: "min", name: "最小值" }
              ]
            },
            markLine: {
              data: [{ type: "average", name: "平均值" }]
            }
          }
        ]
      };
      //初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("echartbox"));

      //使用制定的配置项和数据显示图表
      myChart.setOption(option);
      myChart.on('click', function (params) {
          console.log(params)
      });
    },
    selectChange(arr){

    },
    //获取所有监测项目
    getFactor(){
      this.$http.post("/SiteManagement/index/query").then(res => {
        if (res.data.code == 0) {
          this.factorData = res.data.content.dataList;
          this.factorModel=this.factorData[0].projectCode;
          this.getEchartData()
        }
      });
    },
    //获取绘图数据
    getEchartData(){
       this.loading=true;
       this.dataStatus=''
        this.$http.get("/StatisticsAndAlarm/dataCharts/singleDatasCharts",{
          params:{
            stationCode:this.stationId,//站点编码（mn）'40000000000001'
	          factorCode:this.factorModel,//因子参数（编码）'w01019'
	          startTime:this.dateRange[0],//开始时间
            endTime:this.dateRange[1],//结束时间
            type:'hour',
          }
        }).then(res=>{
            this.loading=false;
          //  initEchart(factor,legendData,xAxisData,seriesData);
          if(res.data.code == 0){
            if(!res.data.content){
                var myChart = this.$echarts.init(document.getElementById("echartbox"));

                //使用制定的配置项和数据显示图表
                myChart.clear();
                this.dataStatus='no';
                return false;
            }else{
              this.dataStatus='';
            }
            let obj = res.data.content.info;
            let legendData=obj.legend;
            let xAxisData=obj.xAxis;
           
            if(obj.series && obj.series.length>0){
                  let factor=obj.series[0].name;
                  let seriesData=obj.series[0].data;
                  let unit=obj.series[0].unit;
                  this.initEchart(unit,factor,legendData,xAxisData,seriesData);
            }
           
           
          }
        })
    },
  },
  computed: {
    stationInfo: {
      get: function() {
        return this.$store.state.stationInfo;
      }
    },
    detailTree:{
      get: function() {
        return this.$store.state.detailTree;
      }
    }
  },
   watch: {
    stationInfo(value) {
      if (value.length > 0) {
        this.stationId = value[0].nodeId;
         
         this.getFactor()
      }
    },
    detailTree(value){
      //监听站点树开启关闭重新绘图
       var myChart = this.$echarts.init(document.getElementById("echartbox"));
       myChart.clear()
    }
  }
};
</script>

<style scope lang="less">
.singlePage {
  .echartbox {
    margin-top: 5px;
    height: 450px;
  }
}
</style>



